import { ComandaService } from './../../../services/comanda/comanda.service';
import { Comanda } from './../../../class/comanda';
import { Component, OnInit } from '@angular/core';
import { DatePipe } from '@angular/common';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

declare interface TableData {
  headerRow: string[];
}


@Component({
  selector: 'app-comanda-list', moduleId: module.id,
  templateUrl: './comanda-list.component.html',
  styleUrls: ['./comanda-list.component.css']
})
export class ComandaListComponent implements OnInit {
  public tableData1: TableData;
  public comandas: Comanda[];
  constructor(private cs: ComandaService) { }

  ngOnInit() {
    this.list();
    this.tableData1 = {
      headerRow: ['ID', 'Nome', 'Data Criação','Valor(R$)', 'Editar', 'Excluir'],
    };
  }
  list() {
    this.cs.returnOrders().then((res) => {
      this.comandas = res.response;
      console.log(this.comandas);
    })
  }
}



