import { PossibilityService } from './../../../services/possibility/possibility.service';
import { Possibility } from './../../../class/possibility';
import { Component, OnInit } from '@angular/core';
declare interface TableData {
  headerRow: string[];
  dataRows: string[][];
}
@Component({
  selector: 'app-possibility-list',
  templateUrl: './possibility-list.component.html',
  styleUrls: ['./possibility-list.component.css']
})
export class PossibilityListComponent implements OnInit {
  public tableData1: TableData;
  public possibilies: Possibility[]
  constructor(private pls: PossibilityService) { }

  ngOnInit() {
    this.list();
    this.tableData1 = {
      headerRow: ['ID', 'Nome', 'Preço','Tipo', 'Editar', 'Excluir'],
      dataRows: [
        ['1', 'Dakota Rice', 'Niger', 'Oud-Turnhout', '$36,738'],
        ['2', 'Minerva Hooper', 'Curaçao', 'Sinaai-Waas', '$23,789'],
        ['3', 'Sage Rodriguez', 'Netherlands', 'Baileux', '$56,142'],
        ['4', 'Philip Chaney', 'Korea, South', 'Overland Park', '$38,735'],
        ['5', 'Doris Greene', 'Malawi', 'Feldkirchen in Kärnten', '$63,542'],
        ['6', 'Mason Porter', 'Chile', 'Gloucester', '$78,615']
      ]
    };
  }

  list() {
    this.pls.returnPossibilities().then((res) => {
      this.possibilies = res.response;
    })
  }

}



