import { ComandaService } from './../../../services/comanda/comanda.service';
import { Comanda } from './../../../class/comanda';
import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-comanda',
  templateUrl: './comanda.component.html',
  styleUrls: ['./comanda.component.css']
})
export class ComandaComponent implements OnInit {
  public id: number;
  public comanda: Comanda;
  constructor(private route: ActivatedRoute, private router: Router, private cs: ComandaService) {

  }

  ngOnInit() {
    this.comanda = new Comanda();
    this.route.params.subscribe(params => {
      this.id = params['id'];
      this.cs.returnOrder(this.id).then((res) => {
        this.comanda = res.response;
        console.log("comanda" +  this.comanda)
      });
    });

  }

}



