import { NotificationService } from './../../../services/notification/notification.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-notitification-create',
  templateUrl: './notitification-create.component.html',
  styleUrls: ['./notitification-create.component.css']
})
export class NotitificationCreateComponent implements OnInit {
  message: string;
  constructor(private ns: NotificationService) { }

  ngOnInit() {
  }

  send() {
    this.ns.send(this.message).then(() => {
      this.message = null;
    }).catch(() => {

    })
  }


}



