import { PromotionsService } from './../../../services/promotions/promotions.service';
import { Promotion } from './../../../class/promotion';
import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { DatePipe } from '@angular/common';

declare interface TableData {
  headerRow: string[];
  dataRows: string[][];
}

@Component({
  selector: 'app-promotions-list',
  templateUrl: './promotions-list.component.html',
  styleUrls: ['./promotions-list.component.css']
})
export class PromotionsListComponent implements OnInit {
  public tableData1: TableData;
  public promotions: Promotion[]
  constructor(private ps: PromotionsService) { }

  ngOnInit() {
    this.list();
    this.tableData1 = {
      headerRow: ['ID', 'Nome', 'Descrição', 'Expira em', 'Editar', 'Excluir'],
      dataRows: [
        ['1', 'Dakota Rice', 'Niger', 'Oud-Turnhout', '$36,738'],
        ['2', 'Minerva Hooper', 'Curaçao', 'Sinaai-Waas', '$23,789'],
        ['3', 'Sage Rodriguez', 'Netherlands', 'Baileux', '$56,142'],
        ['4', 'Philip Chaney', 'Korea, South', 'Overland Park', '$38,735'],
        ['5', 'Doris Greene', 'Malawi', 'Feldkirchen in Kärnten', '$63,542'],
        ['6', 'Mason Porter', 'Chile', 'Gloucester', '$78,615']
      ]
    };
  }
  list() {
    this.ps.list().then((res) => {
      this.promotions = res.response;
    })
  }
}



