import { Promotion } from './../../class/promotion';
import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/Rx';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

@Injectable()
export class PromotionsService {

  constructor(private http: Http) { }
  public url: string = 'http://acess.azurewebsites.net/';
  //public url: string = 'http://localhost:1337/';
  add(promotion: Promotion): any {
    let headers = new Headers({ 'Content-Type': 'application/json' });
    let options = new RequestOptions({ headers: headers });

    let Promotion = {
      Promotion: promotion
    }
    return this.http.post(this.url + 'promotion', Promotion, options).map(res => res.json()).toPromise()
  }

  list(): any {
    return this.http.get(this.url + 'promotion').map(res => res.json()).toPromise()
      .catch(this.handleError);
  }

  private handleError(error: Response | any) {
    // In a real world app, you might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Observable.throw(errMsg);
  }
}



