import { ProductItem } from './productitem';
import { State } from './state';
import { Basic } from './basic';
import { Input } from '@angular/core';

export class Comanda extends Basic {
  @Input()
  price: number;
  @Input()
  endereco: string;
  @Input()
  bairro: string;
  @Input()
  numero: string;
  @Input()
  telefone: string;
  @Input()
  productitens: ProductItem[];
  @Input()
  state: number;


}


