import { ProductsListService } from './../../../services/products/products-list.service';
import { Product } from './../../../class/product';
import { Component, OnInit } from '@angular/core';
import { DatePipe } from '@angular/common';

declare interface TableData {
  headerRow: string[];
  dataRows: string[][];
}

@Component({
  selector: 'app-products-list',
  moduleId: module.id,
  templateUrl: './products-list.component.html',
  styleUrls: ['./products-list.component.css']
})
export class ProductsListComponent implements OnInit {
  public tableData1: TableData;
  public products: Product[];
  constructor(private pls: ProductsListService) { }

  ngOnInit() {
    this.list();
    this.tableData1 = {
      headerRow: ['ID', 'Nome', 'Data Criação','Categoria', 'Editar', 'Excluir'],
      dataRows: [
        ['1', 'Dakota Rice', 'Niger', 'Oud-Turnhout', '$36,738'],
        ['2', 'Minerva Hooper', 'Curaçao', 'Sinaai-Waas', '$23,789'],
        ['3', 'Sage Rodriguez', 'Netherlands', 'Baileux', '$56,142'],
        ['4', 'Philip Chaney', 'Korea, South', 'Overland Park', '$38,735'],
        ['5', 'Doris Greene', 'Malawi', 'Feldkirchen in Kärnten', '$63,542'],
        ['6', 'Mason Porter', 'Chile', 'Gloucester', '$78,615']
      ]
    };
  }
  list() {
    this.pls.returnProducts().then((res) => {
      this.products = res.response;
      console.log(this.products);
    })
  }
}



