import { ComandaComponent } from './components/comanda/comanda/comanda.component';
import { ComandaListComponent } from './components/comanda/comanda-list/comanda-list.component';
import { NotitificationCreateComponent } from './components/notifications/notitification-create/notitification-create.component';

import { PromotionsListComponent } from './components/promotions/promotions-list/promotions-list.component';
import { PromotionsCreateComponent } from './components/promotions/promotions-create/promotions-create.component';
import { PossibilityCreateComponent } from './components/possibility/possibility-create/possibility-create.component';
import { PossibilityListComponent } from './components/possibility/possibility-list/possibility-list.component';
import { CategoryCreateComponent } from './components/category/category-create/category-create.component';
import { CategoryListComponent } from './components/category/category-list/category-list.component';
import { ProductsCreateComponent } from './components/products/products-create/products-create.component';
import { ProductsListComponent } from './components/products/products-list/products-list.component';
import { Routes } from '@angular/router';

import { DashboardComponent } from './dashboard/dashboard.component';
import { UserComponent } from './user/user.component';
import { TableComponent } from './table/table.component';
import { TypographyComponent } from './typography/typography.component';
import { IconsComponent } from './icons/icons.component';
import { MapsComponent } from './maps/maps.component';
import { NotificationsComponent } from './notifications/notifications.component';
import { UpgradeComponent } from './upgrade/upgrade.component';


export const AppRoutes: Routes = [
    {
        path: '',
        redirectTo: '',
        pathMatch: 'full',
    },

    {
        path: 'products',
        component: ProductsListComponent
    },
    {
        path: 'products-create',
        component: ProductsCreateComponent
    },
    {
        path: 'categories',
        component: CategoryListComponent

    },
    {
        path: 'category-create',
        component: CategoryCreateComponent
    }
    , {
        path: 'options',
        component: PossibilityListComponent
    }, {
        path: 'option-create',
        component: PossibilityCreateComponent
    }, {
        path: 'promotion-create',
        component: PromotionsCreateComponent
    },{
        path: 'promotions',
        component: PromotionsListComponent
    },{
        path: 'product-create',
        component: ProductsCreateComponent
    },{
        path: 'notification',
        component: NotitificationCreateComponent
    },{
        path: 'order-list',
        component: ComandaListComponent
    },{
        path: 'order/:id',
        component: ComandaComponent
    },
    {
        path: 'dashboard',
        component: DashboardComponent
    },
    {
        path: 'user',
        component: UserComponent
    },
    {
        path: 'table',
        component: TableComponent
    },
    {
        path: 'typography',
        component: TypographyComponent
    },
    {
        path: 'icons',
        component: IconsComponent
    },
    {
        path: 'maps',
        component: MapsComponent
    },
    {
        path: 'notifications',
        component: NotificationsComponent
    },
    {
        path: 'upgrade',
        component: UpgradeComponent
    },
    {
        path: '**',
        component: UpgradeComponent
    }
]



